<template>
    <v-container>
        <v-form ref="form" v-model="valid">
            <v-sheet class="display-1 mb-5 text-center green accent-2"></v-sheet>
            <v-text-field clearable outlined label="" v-model="restaurant" :rules="[rules.require]"></v-text-field>
            <v-row>
                <v-select outlined label="" v-model="city" :items="item" :rules="[rules.require]"></v-select>
                <v-text-field outlined label="" v-model="postno" :rules="[rules.require, rules.num]"></v-text-field>
            </v-row>
            <v-text-field clearable outlined label="" v-model="address" :rules="[rules.require]"></v-text-field>
            <v-row>
                <v-text-field outlined v-model="open" label="" :rules="[rules.require, rules.time]" placeholder=": 08:30"></v-text-field>
                <v-text-field outlined v-model="close" label="" :rules="[rules.require, rules.time]" placeholder=": 17:00"></v-text-field>
            </v-row>
            

            <v-sheet class="display-1 mt-10 mb-5 text-center green accent-2"></v-sheet>
            <v-row>
                <v-text-field outlined label="" :rules="[rules.require]"></v-text-field>
                <v-text-field outlined label="" :rules="[rules.require]"></v-text-field>
            </v-row>
            <v-text-field clearable outlined label="" v-model="phone" :rules="[rules.require, rules.num]"></v-text-field>
            <v-text-field clearable outlined label="" v-model="email" :rules="[rules.require, rules.email]"></v-text-field>

            <v-divider class="mb-5 mt-10"></v-divider>
            <v-row justify="center">
                <v-btn class="green accen-3 white--text title" @click="Submit()"> </v-btn>
            </v-row>
        </v-form>
    </v-container>
</template>
<script>
export default {
    data: () =>({
        restaurant: "",
        address: "",
        postno: "",
        phone: "",
        email: "",
        open: "",
        close: "",
        city: "",
        item: ["", "", "", ""],
        valid: true,
        rules: { require: value => !!value || "!",
                 num: value => /\d/.test(value) || "!",
                 email: value => /.+@.+/.test(value) || "!",
                 time: value => /\d{2}\:{1}\d{2}/.test(value) || "!"
               }
    }),
    methods:{
        Submit(){
            if (this.valid){
                console.log(this.restaurant, this.address, this.address, this.postno, this.phone, this.email, this.open, this.close);
                console.log(this.city)
            }
            else{
                console.log("Valid Error:", this.valid)
            }
        }
    }
}
</script>